$background_color: #124076;
$lighter_shade: #fcf4f7;
.create_sections {
  width: 100%;
  height: 90%;
  padding: 5px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 10px;

  .add_role {
    width: 100%;
    height: fit-content;

    select{
      padding: 7px 15px;
      outline: none;
      font-size: 1vw;
    }
  }
  .add_permissions {
    width: 100%;
    height: 80%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
    
    .list_of_permissions {
      width: 50%;
      height: 100%;
      overflow-y: scroll;
      border: 1px solid black;
      table {
        width: 100%;
        height: fit-content;
        border-collapse: collapse;
        font-size: 1vw;

        tbody {
          //  border: none;
          .column_name {
            th {
              padding: 8px 15px;
            }
          }
          .selected {
            background-color: gray;
          }
          .column_value {
            // border: none;
            th {
              border-left: none;
              border-right:none ;
              display: flex;
              justify-content: space-between;
              align-items: center;
              padding: 8px 15px;
              text-align: left;
              font-weight: 400;
              button{
                border: none;
                background-color: white;
                font-size: 1.2rem;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
    .added_list_of_permissions {
      width: 50%;
      height: 100%;
      overflow-y: scroll;
      border: 1px solid black;
      table {
        width: 100%;
        height: fit-content;
        border-collapse: collapse;
        font-size: 1vw;
        tbody {
          border-collapse: collapse;
          .column_name {
            th {
              padding: 8px 15px;
            }
          }
          .column_value {
            th {
              border-left: none;
              border-right:none ;
              display: flex;
              justify-content: space-between;
              align-items: center;
              padding: 8px 15px;
              text-align: left;
              font-weight: 400;
              button{
                border: none;
                background-color: white;
                font-size: 1.2rem;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
  .button_ {
    button {
      padding: 8px 15px;
      border-radius: 5px;
      border: none;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
        rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
      cursor: pointer;
      background-color: $background_color;
      color: $lighter_shade;
      font-size: 1vw;
    }
  }
}


